  font-size: 48px;
  font-weight: 600;
  text-align: center;
  color: #fff;
}

.our-departments-cont {
  margin-bottom: 64px;
}

.our-departments-cont .entry-header {
  min-height: 48px;
}

.our-departments-cont .entry-header img {
  margin-right: 16px;
}

.our-departments-cont h3 {
  margin: 0;
  font-size: 24px;
  font-weight: bold;
  color: #fff;
}

.our-departments-cont .entry-content {
  margin-top: 16px;
}

.our-departments-cont .entry-content p {
