/*
# Section: Testimonial
--------------------------------*/
.testimonial-section {
  margin-top: 100px;
  position: relative;
  height: 100%;
  padding: 100px 0;
}

.testimonial-section h2 {
  margin-bottom: 40px !important;
}

.testimonial-section::before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  z-index: -1;
  width: 50%;
  height: 100%;
  background: url('images/testimonial-bg.jpg') no-repeat right center;
  background-size: cover;
}

.testimonial-slider {
  position: relative;
}

.testimonial-slider-wrap {
  position: relative;
  padding: 72px 100px;
  border-radius: 20px;
  background: #fff;
  box-shadow: 40px 20px 27px rgba(0, 0, 0, 0.1);
}

.testimonial-bg-shape {
  position: relative;
}

.testimonial-bg-shape::before {
  content: '';
  position: absolute;
  bottom: -90px;
  left: -130px;
  z-index: -1;
  width: 499px;
  height: 492px;
  background: url('images/testimonial-content-bg.png') no-repeat;
}

.testimonial-slider .content-wrap {
  background: #fff;
}

.testimonial-slider .entry-content p {
  position: relative;
  margin-top: 40px;
  font-size: 16px;
  line-height: 2;
  font-style: italic;
  color: #828282;
}

.testimonial-slider .entry-content p::before {
  content: '';
  position: absolute;
  top: -80px;
  left: 0;
  width: 46px;
  height: 46px;
  background: url('images/testimonial-p-bg.png') no-repeat;
}

.testimonial-slider .testimonial-user {
  display: inline-block;
  margin-top: 24px;
  font-size: 16px;
  font-weight: 500;
  color: #404040;
}

.testimonial-slider .testimonial-user span {
  display: block;
  margin-top: 8px;
  font-size: 12px;
  font-weight: 500;
  color: #828282;
}

.testimonial-slider .user-avatar {
  width: 78px;
  height: 78px;
  margin-top: 56px;
  border-radius: 50%;
  overflow: hidden;
  border: 2px solid #18a3eb;
}

.testimonial-slider .user-avatar img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.swiper-pagination-wrap {
  position: absolute;
  left: 0;
  bottom: 150px;
  z-index: 99;
